//heroes
const H_TYPE = {
    'Tank': '',
    'Mage': '',
    'Assassin': '',
    'Fighter': '',
    'Support': '',
    'Marksman': ''
}
//
const GAME_TYPE = {
    '1': '',
    '3': '',
    '4': '',
    '5': '',
    '6': '',
    '7': '',
    '11': '',
    '23': '',
    '17': '',
    '18': '',
    '19': '',
    '25': '',
    '26': '',
    '27': ''
}
//heroes
const BATLESTYLE = {
    'fz': '',
    'zd': '',
    'sd': '',
    'dy': '',
    'adc': ''
}
//
export const getAreaName = function(id) {
    for (var i in AREAS) {
        if (AREAS[i].id == id) {
            return AREAS[i].name;
        }
    }
}
//
export const gametype = function(id) {
    return GAME_TYPE[id];
}
//heroes
export const htype = function(val) {
    var str = "";
    for (var j = 0; j < val.length; j++) {
        for (var i in H_TYPE) {
            if (i == val[j]) {
                str += H_TYPE[i] + " ";
            }
        }
    }
    return str;

}
//heroes
export const getBatleType = function(val) {
    var str = "";
    for (var j in val) {
        for (var i in BATLESTYLE) {
            if (i == j) {
              str += BATLESTYLE[i] + " : " + val[j]['add-point'];
            }
        }
    }
    return str;
}

export const headUrl = function(id) {
    return 'http://cdn.tgp.qq.com/lol/images/resources/usericon/' + id + '.png';
}
export const headUrlHeroes = function(id) {
    return 'http://cdn.tgp.qq.com/pallas/images/champions_id/' + id + '.png';
}



