import Vue from 'vue'
import Router from 'vue-router'
import Landing from '@/views/landing'//
import Index from '@/views/Index'//index
import News from '@/views/News'//
import Players from '@/views/Players'//
import Heroes from '@/views/Heroes'//
import Video from '@/views/Video'//
import Public from '@/views/publicHeroes'//
import searchild from '@/views/searchild'//
import wardetails from '@/views/wardetails'//

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        component: Landing
    }, {
        path: '/Index',
        component: Index,
        children: [//index
            { path: 'news', component: News },
            { path: 'players', component: Players },
            { path: 'heroes', component: Heroes },
            { path: 'video', component: Video },
            { path: 'hero/:id', component: Public, name: 'hero' },
            //idid
            { path: 'searchild/:tier/:queue/:qquin/:areaid', component: searchild, name: 'searchild' },
            //idid
            { path: 'wardetails/:qquin/:areaid/:gameid', component: wardetails, name: 'wardetails' }
        ]
    }]
})



